.form {
  display: flex;
  flex-direction: column;
  align-items: center;
    box-sizing: border-box;
    background: #ffffff;
    width: 360px;
    padding: 20px 20px ;
    box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;

    @media screen and (min-width: 1280px) {
      width: 460px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 40px;
    }
  };

  .h {
    font-family: 'Manrope';
    font-weight: 500;
    text-align: center;
    margin-bottom: 40px;
    color: #111111;
    letter-spacing: 0.04em;
    @media screen and (max-width: 767px) {
      font-size: 24px;
      line-height: calc(24 / 33);
    }
    @media screen and (min-width: 768px) {
      font-size: 36px;
      line-height: calc(49 / 36);
    }
  }
  
  .wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    @media screen and (max-width: 767px) {
      gap: 24px;
      margin-bottom: 40px;
    }
    @media screen and (min-width: 768px) {
      gap: 32px;
      margin-bottom: 52px;
    }
  }
  

  .label {
    display: flex;
    margin-right: 75px;
    margin-left: 75px;
    flex-direction: row;
    align-items: center;
    /* padding: 12px 16px; */
    gap: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
  }
  .input {
    width: 280px;
    display: flex;
    justify-content: space-between;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    padding: 12px 16px;
    border: 1px solid #54adff;
    border-radius: 40px;

    @media screen and (min-width: 1280px) {
      width: 380px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 40px;
    }
  }
  .input:focus {
    border: 1px solid #accfed;
    border-radius: 10px;
  }
  
  .p {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 60px;
    margin-left: auto;
    margin-right: auto;
    height: 16px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.04em;
    color: #888888;
    @media screen and (max-width: 768px) {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      margin-right: 43px;
      margin-left: 43px;
      margin-bottom: 30px;
    }
  }
  .button {
    width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 28px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: calc(27 / 20);
    display: flex;
    align-items: center;
    letter-spacing: 0.04em;
    color: #ffffff;
    background: #54adff;
    border: none;
    border-radius: 40px;

    @media screen and (min-width: 768px) {
      margin-bottom: 16px;
    }
  }
  .pp {
    height: 27px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;
    margin-right: auto;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
    letter-spacing: 0.04em;
    color: #ffffff;
  }